import React from 'react';
import HomePresenter from './HomePresenter';
import { moviesApi } from '../../api';

export default class HomeContainer extends React.Component {
  state = {
    nowPlaying : null,
    upcoming : null,
    popular : null,
    error : null,
    loading : true,
  }

  async componentDidMount() {
    try{
      /* apiÏóêÏÑú Ìò∏Ï∂ú Îêú data : { results : []} Ïùò Í∞íÏùÑ nowPlaying Ïùò Ïù¥Î¶ÑÏúºÎ°ú Ï†ÄÏû•Ìï®
        ÎßåÏïΩ Îí§Ïóê : nowPlayingÎ•º Ï†ÅÏßÄ ÏïäÎäîÎã§Î©¥ results Ïùò Ïù¥Î¶ÑÏúºÎ°ú Ï†ÄÏû•Îê®
      */
      const { data : { results: nowPlaying } } = await moviesApi.nowPlaying();
      const { data : { results : upComming} } = await moviesApi.upComing();
      const { data : { results : popular} } = await moviesApi.popular();
      // throw Error();
      this.setState({ nowPlaying, upComming, popular })
    } catch {
      this.setState({ error : "Can't find Movie infomation"  })
    } finally {
      this.setState({ loading: false })
    }
  }

  render() {
    const { nowPlaying, upComming, popular, error, loading} = this.state;
    return (
      <HomePresenter 
        nowPlaying={nowPlaying}
        upComming={upComming}
        popular={popular}
        error = {error}
        loading = {loading}
      />
    )
  }
  
}

